<template>
  <div>
    <van-nav-bar
      title="旗舰店"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="mall_list">
      <div class="mall_item" v-for="item in list">
        <img :src="item.imageURL" alt="">
        <div class="mall_item_name">{{item.name}}</div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "index",
    data(){
      return {
        list:[
          {imageURL:'../../../static/images/mall/list_item1.png',name:'中城新国际信用评级有限公司'},
          {imageURL:'../../../static/images/mall/list_item2.png',name:'中债资信评估有限责任公司'},
          {imageURL:'../../../static/images/mall/list_item3.png',name:'国泰君安证劵'},
        ]
      }
    },
    methods:{
      onClickLeft(){

      }
    }
  }
</script>

<style scoped>

  .mall_list {
    position: absolute;
    top: 46px;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 15px;
    background: rgba(247, 248, 250, 1);
  }
  .mall_item {
    width: 100%;
    height: 75px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.19);
    border-radius: 2px;
    margin-bottom: 10px;
    position: relative;
  }
  .mall_item img {
    width: 60px;
    height: 60px;
    position: absolute;
    left: 11px;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .mall_item_name {
    position: absolute;
    top: 25.5px;
    left: 85.5px;
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
    letter-spacing: 0.5px;
  }
</style>



